@tailwind base;
@tailwind components;
@tailwind utilities;
::-webkit-scrollbar{
    height: 12px;
    width: 12px;
}

::-webkit-scrollbar-track{
    background: rgba(255, 255, 255, 0.2);
    border-radius: 1ex;
}

::-webkit-scrollbar-thumb{
    background: skyblue;
    border-radius: 1ex;
}


